.notify{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    position: fixed;
    right: 0;
    background:white ;
    color: black;
    height: fit-content;
    width: 0;
    border-radius: 10px 0 0 10px;
    z-index: 10;
}

.success{
    color: green;
}

.error{
    color: red;
}